.paintingsList{display:grid;grid-template-columns:repeat(3, 1fr);justify-items:center;list-style-type:none;grid-gap:20px;margin-bottom:40px}@media(max-width: 1023px){.paintingsList{grid-template-columns:repeat(2, 1fr)}}@media(max-width: 767px){.paintingsList{grid-template-columns:1fr}}.paintingsList .paintingsListes{position:relative;min-width:30%}.paintingsList .paintingsListes .paintingsImg{width:100%;border-radius:20px}.paintingsList .paintingsListes .additionalInfo{position:absolute;box-sizing:border-box;z-index:2;bottom:0;padding:5px 15px 5px 15px;background:rgba(255,255,255,.75);width:100%;border-bottom-right-radius:20px;border-bottom-left-radius:20px;display:block;font-family:"Roboto";cursor:pointer}.paintingsList .paintingsListes .additionalInfo:hover .additionalFullInfo{display:block;padding-bottom:44px}.paintingsList .paintingsListes .additionalInfo .additionalInfoHeading{font-weight:700;font-size:18px;line-height:20px;text-transform:capitalize;color:#000;margin-bottom:6px}.paintingsList .paintingsListes .additionalInfo .additionalFullInfo{display:none;font-weight:300;font-size:13px;line-height:20px;color:#000}.paintingsList .paintingsListes .additionalInfo .additionalFullInfo .additionalInfoTextСaption{font-weight:bold;font-size:13px;line-height:20px;color:#000;margin-bottom:5px}